.links-container {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 10px;
  margin-bottom: 20px;
}

.link {
  font-family: 'Space Mono', monospace;
  text-decoration: none;
  color: #333;
  padding: 10px;
  background: #e0e0e0;
  border-radius: 8px;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: background 0.3s;
}

.link:hover {
  background: #d0d0d0;
}

.link span {
  margin-left: 8px;
}